export default {
  name: 'UiCheckbox',
  model: {
    prop: 'modelValue',
    event: 'change'
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    },
    error: {
      type: Boolean,
      default: false
    }
  },

  setup (props, { emit }) {
    const changeValue = (e) => {
      emit('change', !props.modelValue)
    }

    return {
      changeValue
    }
  }
}
